<template>
  <div class="Search">
    <button @click="doGet">Get</button>
    <div class="result">{{ someData }}</div>
  </div>
</template>

<script>
  export default {
    name: 'Search',
    data () {
      return {
        searchText: '',
        someData: ''
      }
    },
    methods: {
      doGet: function (input) {
        this.$http.get('').then(response => {
          this.someData = response.body
        })
      }
    }
  }

</script>


