import Vue from 'vue'
import Router from 'vue-router'
import Search from '@/components/Search'
import Welcome from '@/components/Welcome'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Welcome
    },
    {
      path: '/search',
      name: 'Publish Search',
      component: Search
    }
  ]
})



